import axios from 'axios';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useState } from 'react';

export default function TestingSources(){


  //This link:
  //https://www.kindacode.com/article/react-how-to-check-internet-connection-online-offline/
  //Have explanations to detect online and offline status of the network

  //This example link is loading data, its allowed
  //http://[::1]/projects/zlatkoflash/ReactJS_footballstats/the-application-2022_ci4-api/tm/API/LoadTickets



  
  const [urlOfTheSource, setURLOfTheSource] = useState('');
  const [source_url_result, set___source_url_result] = useState('The result');

  const LoadSourceFromTheLink = ()=>{
    if(urlOfTheSource===''){
      alert('Please enter URL for the source');
      return;
    }
    axios.get(urlOfTheSource).then((response)=>{
      console.log('response:', response);
      set___source_url_result( response );
    }).catch(error => {
      //console.log('error:', error);
      set___source_url_result('error');
    });;
  }

  return (
    <div>
      <Container>
        <Row>
          <Col>
            <Form.Control type="text" placeholder="Enter URL of the source" onChange={(e)=>{setURLOfTheSource(e.target.value)}} />
            <Button variant="primary" type="button" onClick={()=>{LoadSourceFromTheLink()}}>
              Load The source to see if is working
            </Button>
            <div style={{backgroundColor:'#fafafa'}}>{
              JSON.stringify(source_url_result)
            }</div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}