
import { createSlice } from "@reduxjs/toolkit";
export const networkSlice = createSlice({
  
  name:'network',
  initialState:{
    type:'not-set-yet',
    is_on:navigator.onLine
  },
  reducers:{
    setOffline:state=>{
      state.is_on=false;
    },
    setOnline:state=>{
      state.is_on=true;
    }
  },

});

export const { setOffline, setOnline } = networkSlice.actions;
export default networkSlice.reducer;