import {
  BrowserRouter as Router,
  Route,
  Routes
} from 'react-router-dom';

import { useSelector, useDispatch } from "react-redux";
import { setOffline, setOnline } from './Redux/Reducers/Network';

import HeaderConnectionStatus from './Components/HeaderConnectionStatus';
import Header from './Components/Header';

import Home from './Pages/Home';
import TestingSources from './Pages/TestingSources';


import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

function App() {

  const networkIsOnline = useSelector( state=>state.network.is_on );
  const dispatch = useDispatch();
  console.log('networkIsOnline:', networkIsOnline);

  window.addEventListener('online', ()=>{
    console.log('Network is online');
    dispatch( setOnline() );
  });
  // Listen to the offline status
  window.addEventListener('offline', ()=>{
    console.log('Network is offline');
    dispatch( setOffline() );
  });

  return (
    <Router>
      <div className="App">
      


        <HeaderConnectionStatus />
        <Header />

        <Routes>
          <Route exact path="/" element={<Home/>} />
          <Route exact path="/TestingSources" element={<TestingSources/>} />
        </Routes>

      </div>
    </Router>
  );
}

export default App;
