import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import { Link } from 'react-router-dom';

import image_header_logo from '../../Assets/Images/Logo.jpg';


export default function Header(){

  const header_links = [
    ['STOPWATCH', '/Stopwatch'],
    ['ICE RENTAL TIMER', '/ICERentalTimer'],
    ['FACILITY SCHEDULING', '/FacilityScheduling'],
    ['SETTINGS', '/Settings'],
    ['TESTING SOURCES', '/TestingSources'],
  ];
  const header_links_html = header_links.map((value, key)=><li key={`header-menu-item-${key}`}>
    <Link to={value[1]}>{value[0]}</Link>
  </li>);

  return (
    <header>
      <Container>
        <Row>
          <Col lg={12}>
            <Link to="/">
              <img src={image_header_logo} alt="Harris TimeInc - DIGITAL SIGNS" />
            </Link>

            <ul>
            {header_links_html}
            </ul>

          </Col>
        </Row>
      </Container>
    </header>
  );
}